define('user/menu/controllers/controllers',[
        'angular', 'core/core',
        'user/menu/controllers/userManageOrdersController',
        'user/menu/controllers/anonymousUserMenuController',
        'user/menu/controllers/menu/userMenuController',
        'user/menu/controllers/menu/userMenuCourseController',
        'user/menu/controllers/menu/userMenuViewController',
        'user/menu/controllers/orders/userOrdersController',
        'user/menu/controllers/orders/userOrderController'
    ],
    function (angular, core,
        userManageOrdersController, anonymousUserMenuController,
        userMenuController, userMenuCourseController, userMenuViewController,
        userOrdersController, userOrderController) {
        'use strict';

        var dependencies = [core.name];
        var controllers = angular.module('lunchtime.user.menu.controllers', dependencies);

        controllers.controller('userManageOrdersController', userManageOrdersController);
        controllers.controller('anonymousUserMenuController', anonymousUserMenuController);

        // Menu
        controllers.controller('userMenuController', userMenuController);
        controllers.controller('userMenuCourseController', userMenuCourseController);
        controllers.controller('userMenuViewController', userMenuViewController);

        // Orders
        controllers.controller('userOrdersController', userOrdersController);
        controllers.controller('userOrderController', userOrderController);

        return controllers;
    });