define('user/menu/directives/menu/userMenuCourse',[], function() {
    'use strict';

    var userMenuCourse = [
        function() {

            return {
                restrict: 'AE',
                templateUrl: '/template/user/menu/menu/course.tmpl',
                scope: {
                    course: '=',
                    isFreezed: '=',
                    isWished: '=',
                    onOrderCourse: '&',
                    onWishCourse: '&'
                },
                controller: 'userMenuCourseController'
            };
        }
    ];

    return userMenuCourse;
});