define('admin/menu/menu',['angular',
        'admin/menu/controllers/controllers',
        'admin/menu/services/services',
        'admin/menu/filters/filters',
        'admin/menu/directives/directives',
        'admin/menu/providers/providers',
        'admin/menu/templates/templates'],
    function(angular, controllers, services, filters, directives, providers, templates) {
        'use strict';

        var dependencies = [
            controllers.name,
            services.name,
            filters.name,
            directives.name,
            providers.name,
            templates.name
        ];

        var template = angular.module('lunchtime.admin.menu', dependencies);

        return template;
    });