define('user/menu/filters/boxTitleFilter',['framework/assert'], function(assert) {
    'use strict';

    var boxTitleFilter = [function () {
        return function(box) {
            if (!box) return '';

            if (box.index === 0)
                return '';

            return ' #{0}'.format(box.index);            
        };
    }];

    return boxTitleFilter;
});