define('admin/sales/directives/adminSalesUsersOrders',[], function() {
    'use strict';

    var adminSalesUsersOrders = [
        function() {

            return {
                restrict: 'AE',
                templateUrl: '/template/admin/sales/users-orders.tmpl',
                scope: {
                    date: '=',
                    usersOrders: '=',
                    onPurchaseOrder: '&',
                    onUndoPurchaseOrder: '&',
                    onRemoveOrder: '&'
                },
                controller: 'adminSalesUsersOrdersController'
            };
        }
    ];

    return adminSalesUsersOrders;
});