define('user/menu/directives/orders/userOrders',[], function() {
    'use strict';

    var userOrders = [function() {

        return {
            restrict: 'AE',
            templateUrl: '/template/user/menu/orders.tmpl',
            scope: {
                orders: '=',
                date: '=',
                isFreezed: '=',
                onRemoveOrder: '&',
                onMoveOrderToBox: '&'

            },
            controller: 'userOrdersController'
        };
    }];

    return userOrders;
});