define('user/menu/directives/orders/userOrder',[], function() {
    'use strict';

    var userOrder = [function () {

        return {
            restrict: 'AE',
            templateUrl: '/template/user/menu/orders/order.tmpl',
            scope: {
                order: '=',
                isFreezed: '=',
                boxes: '=',
                onRemoveOrder: '&',
                onMoveOrderToBox: '&'
            },
            controller: 'userOrderController'
        };
    }];

    return userOrder;
});