define('user/menu/directives/directives',[
        'angular',
        'user/menu/directives/menu/userMenu',
        'user/menu/directives/menu/userMenuCourse',
        'user/menu/directives/menu/userMenuView',
        'user/menu/directives/orders/userOrders',
        'user/menu/directives/orders/userOrder'
    ],
    function(angular, userMenu, userMenuCourse, userMenuView, userOrders, userOrder) {
        'use strict';

        var dependencies = [];
        var directives = angular.module('lunchtime.user.menu.directives', dependencies);

        // Menu
        directives.directive('ltUserMenu', userMenu);
        directives.directive('ltUserMenuCourse', userMenuCourse);
        directives.directive('ltUserMenuView', userMenuView);

        // Orders
        directives.directive('ltUserOrders', userOrders);
        directives.directive('ltUserOrder', userOrder);

        return directives;
    });