define('user/menu/directives/menu/userMenu',[], function() {
    'use strict';

    var userMenu = [function () {

        return {
            restrict: 'AE',
            templateUrl: '/template/user/menu/menu.tmpl',
            scope: {
                courses: '=',
                wishedCourses: '=',
                date: '=',
                isFreezed: '=',
                onOrderCourse: '&'
            },
            controller: 'userMenuController'
        };
    }];

    return userMenu;
});