define('user/menu/directives/menu/userMenuView',[], function() {
    'use strict';

    var userMenuView = [
        function() {

            return {
                restrict: 'AE',
                templateUrl: '/template/user/menu/view.tmpl',
                scope: {
                    courses: '=',
                    date: '='
                },
                controller: 'userMenuViewController'
            };
        }
    ];

    return userMenuView;
});